  padding: 0;
  margin: 0;
}

body {
  font-family: 'Poppins', sans-serif;
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}
.skip-link {
  position: absolute;
  top: -40px; /* Hide it above the viewport */
  left: 10px;
  background-color: #db0000;
  color: white;
  padding: 8px 16px;
  font-size: 16px;
  text-decoration: none;
  border-radius: 4px;
  z-index: 1000;
  transition: top 0.3s ease;
}

.skip-link:focus {
  top: 10px; /* Bring it into view when focused */
  outline: none; /* Remove default outline */
}

/*
  Restaurant Header
*/
.restaurant {
  padding: 8px 16px;
  background-color: white;
  display: grid;
  grid-template-columns: auto 1fr auto;
  gap: 10px;
  position: sticky;
  top: 0;
  z-index: 99;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
}

.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #db0000;
  
  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;
  
  font-size: 18px;
  color: white;
  cursor: pointer;
}

.restaurant .restaurant__menu {
  display: flex;
  align-items: center;
}

.restaurant .restaurant__menu button {
  background-color: transparent;
  border: none;
  font-size: 18px;
  padding: 8px;
  cursor: pointer;
}

.restaurant .restaurant__brand {
  display: flex;
  align-items: center;
}

.restaurant .restaurant__brand h1 {
  color: #121211;
  text-transform: uppercase;
  font-size: 22px;
  user-select: none;
}

.restaurant .restaurant__navigation {
  position: absolute;
  top: 50px;
  left: -180px;
  width: 150px;
  transition: all 0.3s;
  padding: 8px;
  background-color: white;
  overflow: hidden;
}

.restaurant .restaurant__navigation.open {
  left: 0;
}

.restaurant .restaurant__navigation ul li a {
  display: inline-block;
  text-decoration: none;
  color: black;
  padding: 8px;
  margin-bottom: 5px;
  width: 100%;
}

/*
  Main Content
*/
main {
  padding: 32px;
  flex: 1;
}

.content {
  margin: 0 auto;
  min-height: 100%;
}

.content .content__heading {
  font-weight: normal;
}

/*
  Restaurant List (using Flexbox)
*/
.restaurants {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  margin: 32px 0;
  justify-content: space-between;
}

/*
  Restaurant Item
*/
.restaurant-item {
  flex: 1 1 calc(100% - 16px); /* Full-width by default */
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  overflow: hidden;
  margin-bottom: 20px;
  transition: transform 0.3s ease;
}

.restaurant-item:hover {
  transform: scale(1.02); /* Slight zoom-in effect on hover */
}

.restaurant-item__header {
  position: relative;
}

.restaurant-item .restaurant-item__header .restaurant-item__header__poster {
  width: 100%;
  height: 200px; /* Set a fixed height for consistent card size */
  object-fit: cover;
}

.restaurant-item .restaurant-item__header .restaurant-item__header__rating {
  position: absolute;
  padding: 8px;
  bottom: 10px;
  left: 10px;
  display: inline-block;
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  border-radius: 4px;
}

.restaurant-item .restaurant-item__header .restaurant-item__header__rating__score {
  margin-left: 5px;
}

.restaurant-item .restaurant-item__content {
  padding: 16px;
}

.restaurant-item .restaurant-item__content h3 {
  margin: 0 0 10px 0;
}

.restaurant-item .restaurant-item__content h3 a {
  color: #121211;
  text-decoration: none;
}

.restaurant-item .restaurant-item__content p {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4; /* number of lines to show */
  -webkit-box-orient: vertical;

  /* Standard property for future compatibility */
  line-clamp: 4;
}

/*
  Restaurant Detail (using Flexbox Grid)
*/
.restaurant-detail {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-top: 20px;
}

.restaurant-detail__image {
  flex: 1 1 100%; /* Full-width by default */
  max-width: 600px;
}

.restaurant-detail__image img {
  width: 100%;
  border-radius: 8px;
  object-fit: cover;
}

.restaurant-detail__info {
  flex: 1 1 calc(100% - 20px); /* Full-width by default */
}

.restaurant-detail__info h2 {
  margin-bottom: 16px;
  color: #333;
}

.restaurant-detail__info p {
  margin-bottom: 12px;
  color: #555;
}

/*
  Footer
*/
footer {
  padding: 16px;
}

footer p {
  text-align: center;
  color: #aaaaaa;
}

footer p a {
  color: #121211;
  text-decoration: none;
}

/* Responsive Media Queries */
@media screen and (min-width: 600px) {
  .restaurant-item {
    flex: 1 1 calc(50% - 16px); /* Two columns on medium screens */
  }

  .restaurant-detail__image {
    flex: 1 1 calc(50% - 10px); /* 50% width on medium screens */
  }

  .restaurant-detail__info {
    flex: 1 1 calc(50% - 10px); /* 50% width on medium screens */
  }
}

@media screen and (min-width: 900px) {
  .restaurant-item {
    flex: 1 1 calc(33.33% - 16px); /* Three columns on large screens */
  }

  .restaurant-detail__image,
  .restaurant-detail__info {
    flex: 1 1 calc(50% - 10px); /* Even split between image and info */
  }
}

@media screen and (min-width: 1200px) {
  .restaurant-item {
    flex: 1 1 calc(25% - 16px); /* Four columns on extra large screens */
  }

  .restaurant-detail__image,
  .restaurant-detail__info {
    flex: 1 1 calc(50% - 10px); /* Even split between image and info */
  }
}
